<script>
export default {
  name: "Header",
  props: {
    msg: String
  },
  data() {
    return {
      width: 0,
      height: 0,
      isTest: true,
      isActive: true
    };
  },
  methods: {
    fn_search: function() {
      if (this.searchActive) {
        this.$store.commit("searchActive", false);
      } else {
        this.$store.commit("searchActive", true);
      }
    },
    fn_menu: function() {
      if (!this.isMenu) {
        this.$store.commit("isMenu", true);
      } else {
        this.$store.commit("isMenu", false);
      }
    },
    fn_logout: function() {
      this.$store.commit("userInfo", "");
      console.log("logout", this.$store.state);
    },
    fn_error: function(event) {
      console.log(event);
    }
  },
  computed: {
    loginState() {
      let state = false;
      if (this.$store.state.userInfo.length > 0) {
        state = this.$store.state.userInfo[0].isLogin;
      }
      return state;
    },
    cartItemsLength() {
      return this.$store.state.cartProducts.length;
    },
    searchActive() {
      return this.$store.state.searchActive;
    },
    isMenu() {
      return this.$store.state.isMenu;
    }
  }
};
</script>

