<template>
  <section class="contents-wrap">
    <div class="container">
      <h2 class="title">login</h2>
      <form v-on:submit="onSubmit">
        <div class="input-group">
          <div class="form-input">
            <input
              type="text"
              id="USRID"
              class="input"
              v-model="USRID"
              placeholder="your id"
              v-model.lazy="USRID"
            />
            <label for="USRID">your id</label>
          </div>
          <div class="form-input">
            <input
              type="password"
              id="SCRT_NO"
              class="input"
              autocomplete="on"
              v-model="SCRT_NO"
              placeholder="password"
            />
            <label for="SCRT_NO">password</label>
          </div>
          <div class="form-select">
            <select class="select" v-model="LANG_CD">
              <option
                v-for="(option, index) in LANG"
                v-bind:key="index"
                v-bind:value="option.value"
              >{{ option.value }}</option>
            </select>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
              <path fill="none" d="M0 0h24v24H0z" />
              <path fill="#fff" d="M5 11h14v2H5z" />
            </svg>
          </div>
          <button class="btn">get data</button>
        </div>
      </form>
      <p>id:{{USRID}} / pw:{{SCRT_NO}} / lang:{{LANG_CD}}</p>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      USRID: null,
      SCRT_NO: null,
      LANG_CD: null || "KOR",
      LANG: [{ value: "KOR" }, { value: "USA" }, { value: "JPN" }],
      isLogin: null,
      userInfo: null
    };
  },
  computed: {
    //  getter
    reversedMessage: function() {
      // `this`  vm  .
      return this.$store.state.userInfo;
    }
  },
  methods: {
    async onSubmit() {
      //event.preventDefault();
      const user = await this.postUser();
      if (user.data) {
        this.$store.commit("userInfo", user.data);
      }
      this.SCRT_NO = null;
    },
    async postUser() {
      try {
        return await this.$axios({
          method: "post",
          url: "/EosAjax.do",
          params: {
            BeanId: "sys.loginService",
            Method: "actionLogin",
            USRID: this.USRID,
            SCRT_NO: this.SCRT_NO,
            LANG_CD: this.LANG_CD
          }
        }); /*.then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });*/
      } catch (error) {
        console.error(error);
      }
    }
  },
  created: function() {
    // `this`  vm  .
    console.log("login");
  }
};
</script>

