          image: ""
        }
      ]
    };
  },
  created() {
    this.$store.commit("orderStep", 3);
    //this.$store.commit("searchActive", false);
    this.partsItem = this.fn_item(this.type);
    return;
  },
  methods: {
    fn_item: function(_value) {
      return this.items.filter(function(item) {
        return item.idx + "" === _value + "";
      });
    },
    fn_tab: function(_value) {
      this.selected = _value;
    }
  }
};
