<template>
  <div id="app" v-bind:class="[orderStep === 0 ? 'active' : '']">
    <header>
      <SearchComponent v-if="searchActive" calendarActive />
      <HeaderComponent />
      <h2 class="title">
        <span>Easy</span>
        <span>Ordering</span>
        <span>System</span>
      </h2>
    </header>
    <main>
      <router-view />
    </main>
    <footer>
      <StepComponent />
      <FooterComponent />
    </footer>
    <GuideComponent />
  </div>
</template>
    
<script>
import HeaderComponent from "@/components/Header.vue";
import FooterComponent from "@/components/Footer.vue";
import SearchComponent from "@/components/Search.vue";
import StepComponent from "@/components/Step.vue";
import GuideComponent from "@/components/Guide.vue";

export default {
  components: {
    HeaderComponent,
    FooterComponent,
    SearchComponent,
    StepComponent,
    GuideComponent
  },
  data() {
    return {
      width: 0,
      height: 0,
      isTest: true,
      isActive: true,
      isMenu: false
    };
  },
  computed: {
    scrollY() {
      return this.$store.state.scrollY;
    },
    searchActive() {
      return this.$store.state.searchActive;
    },
    orderStep() {
      return this.$store.state.orderStep || 0;
    }
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("scroll", this.fn_scroll);
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
    window.removeEventListener("scroll", this.fn_scroll);
  },
  methods: {
    handleResize() {
      this.width = window.innerWidth;
      this.height = window.innerHeight;
    },
    fn_scroll() {
      this.$store.commit("scrollY", window.scrollY);
    },
    Upper: function(value) {
      return value.toUpperCase();
    },
    Lower: function(value) {
      return value.toLowerCase();
    }
  },
  watch: {
    $route(to, from) {
      this.$store.commit("orderStep", 100);
      this.$store.commit("isMenu", false);
      console.log(from.name, " --> ", to.name);
    }
  },
  filters: {
    // Filter definitions
    Upper(value) {
      return value.toUpperCase();
    }
  }
};
</script>
    
